import guideApi from 'api/guideApi';
import Loading from 'shared/components/Loading';
import {
  ContentState,
  convertFromHTML,
  convertToRaw,
  EditorState,
} from 'draft-js';
import draftToHtml from 'draftjs-to-html';
import { useEffect, useState } from 'react';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { ToastWrapper } from 'utils';
import { useParams } from "react-router-dom";
import styles from './update.module.css';

function Update(props) {
  const id = useParams().guideId;
  const [guide, setGuide] = useState(null);
  const [updating, setUpdating] = useState(false);
  const [editorState, setEditorState] = useState(EditorState.createEmpty());
  const hashtagConfig = {
    trigger: '#',
    separator: ' ',
  };

  useEffect(() => {
    if (id) {
      guideApi
        .getGuideById(id)
        .then((res) => {
          if (res.data.content) {
            const blocksFromHTML = convertFromHTML(res.data.content);
            console.log(blocksFromHTML);
            const state = ContentState.createFromBlockArray(
              blocksFromHTML.contentBlocks,
              blocksFromHTML.entityMap
            );
            setEditorState(EditorState.createWithContent(state));
          }
          setGuide(res.data);
        })
        .catch((err) => console.log(err));
    } else {
      alert('Khng tm thy bi vit!');
    }
  }, []);

  const onEditorStateChange = (editorState) => {
    setEditorState(editorState);
  };

  const onSubmit = (e) => {
    e.preventDefault();
    setUpdating(true);
    const rawContentState = convertToRaw(editorState.getCurrentContent());
    const content = draftToHtml(rawContentState, hashtagConfig, true);
    const title = document.getElementById('formTitle').value;
    const priority = parseInt(document.getElementById('formPriority').value);

    if (!title) {
      setUpdating(false);
      return alert('Vui lng nhp y  tiu  v ni dung bi vit!');
    }

    if (!priority || priority < 0) {
      setUpdating(false);

      return alert(' u tin phi l s ln hn 0!');
    }

    guideApi
      .updateGuide(id, { priority, title, content, updatedAt: new Date() })
      .then((res) => {
        setUpdating(false);

        if (res.data) {
          return ToastWrapper('Cp nht bi vit thnh cng!');
        }
      })
      .catch((err) => {
        setUpdating(false);
        console.log(err);
      });
  };

  if (!guide) {
    return <Loading />;
  }

  return (
    <div className={styles.container}>
      <p>
        Ngy to: {new Date(guide.createdAt).toLocaleDateString('en-GB')},{' '}
        {new Date(guide.createdAt).toLocaleTimeString('en-GB')}
      </p>
      <p>
        Ngy cp nht: {new Date(guide.updatedAt).toLocaleDateString('en-GB')},{' '}
        {new Date(guide.updatedAt).toLocaleTimeString('en-GB')}
      </p>
      <form onSubmit={onSubmit} className={styles.formContainer}>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}> u tin</label>
          <input
            className={styles.formInput}
            id='formPriority'
            placeholder='Nhp s ln hn 0'
            defaultValue={guide.priority}
          />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Tiu </label>
          <input
            className={styles.formInput}
            id='formTitle'
            placeholder='Nhp tiu  bi vit'
            defaultValue={guide.title}
          />
        </div>
        <label className={styles.formLabel}>Ni dung</label>
        <div className={styles.editorContainer}>
          <Editor
            editorState={editorState}
            toolbarClassName='toolbarClassName'
            wrapperClassName='wrapperClassName'
            editorClassName='editorClassName'
            onEditorStateChange={onEditorStateChange}
          />
        </div>
        {updating ? (
          <span className={styles.submitButton}>ang cp nht...</span>
        ) : (
          <input
            className={styles.submitButton}
            type='submit'
            value={'Cp nht bi vit'}
          />
        )}
      </form>
    </div>
  );
}

export default Update;
